<template>
  <div class="header">
    <div class="logo_content">
      <span class="span_icon" @click="showMenu">
        <i class="el-icon-menu icon"></i>
      </span>
      <img src="../assets/images/swissmic-logo-white.png" alt="">
      <span class="logo_title">MS</span>
    </div>
    <div class="user_content">
      <span>{{$t('common.welcome')}}</span>
      <div class="userset">
        <!-- <el-dropdown  :hide-on-click="true"> -->
        <span class="el-dropdown-link username">
          {{username}}
        </span>
      <!-- </el-dropdown> -->
      </div>
      <span class="logout_span" @click="loginOut">[ {{$t('common.logout')}} ]</span>
    </div>
  </div>
</template>
<script>
  import { mapMutations } from 'vuex'
  export default {
    data() {
      return {
        username: localStorage.getItem('user_name')||'',
      }
    },
    methods:{
      ...mapMutations(['changeCollapse']),
      showMenu(){
        let _this = this;
        this.changeCollapse({isCollapse:_this.$store.state.isCollapse})
      },
      loginOut(){
        localStorage.removeItem('user_name');
        localStorage.removeItem('user_group_power');
        localStorage.removeItem('user_id');
        this.$router.push('/login')  
        setTimeout(() => {
          location.reload() // 强制刷新
        }, 500)     
      }
     
    }
  }
</script>
<style scoped>
  .header {
   /* position: relative;*/
    box-sizing: border-box;
    width: 100%;
    height: 42px;
    font-size: 18px;
    line-height: 42px;
    color: #fff;
    background:rgb(43,45,52);
  }
  .logo_content{
    float: left;
    margin:4px 0 0 20px;
    height: 26px;
    line-height: 26px;
  }
  .span_icon{
    font-size: 26px;
    display: inline-block;
    padding-top: 2PX;
    cursor: pointer;
  }
  .span_icon:hover{
    color: #ffd04b;
  }
  .logo_title{
    font-size: 16px;
  }
  .logo_content img {
    width:90px;
    height: 26px;
  }
  .user_content {
    float: right;
    margin-right: 30px;
    font-size: 13px;
    display: flex;
  }
  .logout_span{
    margin-left: 20px;
    cursor: pointer;
  }
  .logout_span:hover{
    color: #ffd04b;
  }
  .username{
    color: #fff;
    margin-left: 10px;
    cursor: pointer;
  }

</style>