<template>
  <div class="sidebar" :class="this.$store.state.isCollapse?'sidebarhidden':'sidebarshow'">
    <el-menu 
        :collapse="this.$store.state.isCollapse"
        :default-active="activeRouter"
        class="el-menu-vertical-demo"
        router
        :unique-opened="true"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b">
       <el-scrollbar style='height:100%'> 
      <template v-for="item in supitems">
        <template v-if="item.childMenus">
          <el-submenu :index="item.router">
            <template slot="title" >
              <i :class="item.icon"></i>
              <span style="font-size:16px;margin-left:12px;">{{ item.name }}</span>
            </template>       
            <el-menu-item  v-for="(subItem,i) in item.childMenus" :key="i" :index="subItem.router">
              {{ subItem.name }}
            </el-menu-item>
          </el-submenu>
        </template>
        <template v-else>
          <el-menu-item :index="item.router">
            <i :class="item.icon"></i>
            <span style="margin-left:12px;">{{ item.name }}</span>
          </el-menu-item>
        </template>
      </template>     
      </el-scrollbar>     
    </el-menu>
    
  </div> 
</template>

<script>
    import {
        surpermission
    } from '@/utils/public'
export default {
  data() {
    return {
        surpermissionFlag: surpermission(),
        isCollapse:this.$store.state.isCollapse,
        activeRouter:this.$route.path.replace(/\//g,''),
        items: [
           
        ],
        supitems:[
			{
				id: 1,        
				// name: this.$t('menu.report'),
				name:"报表统计",
				sort: 1,
				icon:"el-icon-pie-chart",
				router: "report",
				childMenus:null,
				parentId: null
			},
            {
                id: 2,
                name: this.$t('menu.logsmanage'),
                sort: 1,
                router: "logsmanage",
