                childMenus: [
                    {
                        id: 21,
                        name: this.$t('menu.logsexport'),
                        sort: 1,
                        router: "exportlog",
                        childMenus: null,
                        parentId: 2
                    }
                ],
                parentId: null
            },
			{
			    id: 3,
			    name: this.$t('menu.usermanage'),
			    sort: 1,
			    router: "usermanage",
			    icon:"el-icon-s-custom",
			    childMenus: [
			        {
			            id: 31,
			            name: this.$t('menu.user'),
			            sort: 1,
			            router: "user",
			            childMenus: null,
			            parentId: 3
			        }
			    ],
			    parentId: null
			},
			{
			    id: 4,
			    name: this.$t('menu.files'),
			    sort: 1,
			    router: "filemanage",
			    icon:"el-icon-folder-opened",
			    childMenus: [
					{
					    id: 42,
					    name: this.$t('menu.upload'),
					    sort: 2,
					    router: "upload",
					    childMenus: null,
					    parentId: 4
					}
			    ],
			    parentId: null
			}
        ]

    }
  },
  created() {
      // if (this.surpermissionFlag) {
      //   this.items = this.supitems
      // } else {
      //   this.items = this.noritems
      // }
    }
}
</script>
<style>
    .sidebarshow{
        width:200px;
    }
    .el-menu-vertical-demo .el-scrollbar__wrap {
    overflow-x: hidden;
  }
  .el-scrollbar__wrap {
    overflow-x: hidden;
  }
    .sidebarhidden{
        width:64px;
    }
    .sidebar{
        display: block;
        position: absolute;
        /*width: 200px;*/
       /* height: 100%;*/
        left: 0;
        top: 42px;
        bottom:0;
        box-sizing: border-box;
      }

  .sidebar > ul {
    height:100%;
  }
  .el-menu{
    text-align: left;
  }
  .el-menu-item{
    font-size: 16px!important;
  }
  .el-scrollbar__wrap {
   overflow-x: hidden;
}
.sidebar .el-submenu__title{
    font-size: 16px!important;
}
  .el-menu-vertical-demo{
    height: 100%;
  }

</style>